.buttonIcon {
    display: flex;
    /* border: 4px #fff solid; */
    width: fit-content;
    height: fit-content;
    cursor: pointer;
  }
  
  .icon {
    background-color: #fff;
    padding: 10px 10px 5px 10px;
  }
  
  .icon svg {
    width: 25px;
    height: 25px;
  }
  
  .cube {
    transition: transform 0.5s; /* Adjust transition property */
    transform-style: preserve-3d;
    width: 200px;
    height: 20px;
  }
  
  .buttonIcon:hover {
    /* border-color: #ff98a2; */
  }
  
  .cube:hover {
    transform: rotateX(90deg);
  }
  
  .side {
    position: absolute;
    height: 47px;
    width: 200px;
    display: flex;
    /* font-size: 0.8em; */
    justify-content: center;
    /* align-items: center; */
    text-transform: uppercase;
    letter-spacing: 0.5px;
    font-weight: bold;
  }
  
  .top {
    background: #ff98a2;
    color: #fff;
    transform: rotateX(-90deg) translate3d(0, 13.5px, 2em);
  }
  
  .front {
    background: #222229;
    color: #fff;
    transform: translate3d(0, 0, 1em);
  }
  